<template>
	<view class="auth-container">
		<!-- È°∂ÈÉ®logo -->
		<view class="auth-header">
			<image class="logo" src="/static/logo.png" mode="aspectFit" />
			<text class="title">Ê¨¢Ëøé‰ΩøÁî®Ê¥•‰πêÊ∏∏</text>
		</view>

		<!-- ÁôªÂΩïË°®Âçï -->
		<view class="auth-form">
			<!-- Áî®Êà∑ÂêçËæìÂÖ• -->
			<view class="form-item" :class="{ error: errors.username }">
				<uni-icons type="person" size="20" color="#2867CE" />
				<input 
					v-model="form.username"
					placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑/Áî®Êà∑Âêç"
					placeholder-class="placeholder"
					maxlength="20"
					@focus="resetError('username')"
				/>
			</view>
			<text v-if="errors.username" class="error-tip">{{ errors.username }}</text>
			<!-- ÂØÜÁ†ÅËæìÂÖ• -->
			<view class="form-item" :class="{ error: errors.password }">
				<uni-icons type="locked" size="20" color="#2867CE" />
				<input
					v-model="form.password"
					placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
					placeholder-class="placeholder"
					:password="!showPassword"
					maxlength="20"
					@focus="resetError('password')"
				/>
				<view class="eye-icon" @click="showPassword = !showPassword">
					<uni-icons :type="showPassword ? 'eye' : 'eye-slash'" size="20" color="#2867CE" />
				</view>
			</view>
			<text v-if="errors.password" class="error-tip">{{ errors.password }}</text>
			<!-- Ë°®ÂçïÊìç‰Ωú -->
			<view class="form-actions">
				<view class="remember-me">
					<checkbox-group @change="toggleRemember">
						<label>
							<checkbox :checked="rememberMe" color="#2867CE" />
							<text>ËÆ∞‰ΩèÊàë</text>
						</label>
					</checkbox-group>
				</view>
				<text class="forgot-password" @click="navigateTo('/pages/forgot-password/index')">ÂøòËÆ∞ÂØÜÁ†ÅÔºü</text>
			</view>
			<!-- ÁôªÂΩïÊåâÈíÆ -->
			<button 
				class="auth-btn primary"
				:class="{ disabled: !formValid }"
				:disabled="!formValid || loading"
				@click="handleLogin"
			>
				<text v-if="!loading">Áôª ÂΩï</text>
				<uni-load-more v-else status="loading" :icon-size="16" />
			</button>
			<!-- Ê≥®ÂÜåÂºïÂØº -->
			<view class="auth-footer">
				<text>ËøòÊ≤°ÊúâË¥¶Âè∑Ôºü</text>
				<text class="link" @click="navigateTo('/pages/register/index')">Á´ãÂç≥Ê≥®ÂÜå</text>
			</view>
		</view>

		<!-- Á¨¨‰∏âÊñπÁôªÂΩï -->
		<view class="social-login">
			<view class="divider">
				<text>ÊàñÈÄöËøá‰ª•‰∏ãÊñπÂºèÁôªÂΩï</text>
			</view>
			<view class="social-icons">
				<button class="icon-btn" open-type="getPhoneNumber" @getphonenumber="onWechatLogin">
					<uni-icons type="weixin" size="24" color="#09BB07" />
				</button>
			</view>
		</view>

		<!-- ÂçèËÆÆÂ£∞Êòé -->
		<view class="agreement">
			<checkbox-group @change="toggleAgreement">
				<label>
					<checkbox :checked="agreed" color="#4cd964" />
					<text>ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè</text>
				</label>
			</checkbox-group>
			<text class="link" @click="showAgreement">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
			<text>Âíå</text>
			<text class="link" @click="showPrivacy">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
		</view>
	</view>
</template>

<script>
import { http } from '@/api/http'
import { mapMutations } from 'vuex'

export default {
	data() {
		return {
			form: {
				username: '',
				password: ''
			},
			errors: {
				username: '',
				password: ''
			},
			showPassword: false,
			rememberMe: false,
			agreed: true,
			loading: false
		}
	},
	computed: {
		formValid() {
			return (
				this.form.username && 
				this.form.password && 
				!this.errors.username && 
				!this.errors.password &&
				this.agreed
			)
		}
	},
	onLoad() {
		this.loadRememberedAccount()
	},
	methods: {
		...mapMutations("user", ["SET_INFO"]),
    
		// ÂàùÂßãÂåñËÆ∞‰ΩèÁöÑË¥¶Âè∑
		loadRememberedAccount() {
			const account = uni.getStorageSync('rememberedAccount')
			if (account) {
				this.form = { ...account }
				this.rememberMe = true
			}
		},
    
		// È™åËØÅË°®Âçï
		validate() {
			let valid = true
			const { username, password } = this.form
			if (!username.trim()) {
				this.errors.username = 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç/ÊâãÊú∫Âè∑'
				valid = false
			} else if (!/^[\w\u4e00-\u9fa5]{3,20}$/.test(username)) {
				this.errors.username = 'Áî®Êà∑ÂêçÊ†ºÂºè‰∏çÊ≠£Á°Æ'
				valid = false
			}
      
			if (!password) {
				this.errors.password = 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å'
				valid = false
			} else if (password.length < 6) {
				this.errors.password = 'ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë6‰Ωç'
				valid = false
			}
      
			return valid
		},
    
		// ÈáçÁΩÆÈîôËØØ
		resetError(field) {
			this.errors[field] = ''
		},
    
		// ËÆ∞‰ΩèË¥¶Âè∑
		toggleRemember(e) {
			this.rememberMe = e.detail.value.length > 0
		},
    
		// ÂçèËÆÆÂãæÈÄâ
		toggleAgreement(e) {
			this.agreed = e.detail.value.length > 0
		},
    
		// Â§ÑÁêÜÁôªÂΩï
		async handleLogin() {
		    if (!this.validate()) return;
		
		    this.loading = true;
		    try {
		        const res = await http.post('/api/auth/login', {
		            account: this.form.username,
		            password: this.form.password
		        });
				console.log('üîç ÂÆåÊï¥ÂìçÂ∫îÁªìÊûÑ:', JSON.stringify(res, null, 2));
		        // Â≠òÂÇ®tokenÂíåÁî®Êà∑‰ø°ÊÅØ
		        uni.setStorageSync('token', res.data.token);
		        this.SET_INFO(res.data.user); 
				// Êñ∞Â¢ûÔºöÂ≠òÂÇ®userIdÂíåavatarÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('userId', res.data.user.userId);
				uni.setStorageSync('avatar', res.data.user.avatar); // ÂêåÊó∂Â≠òÂÇ®Â§¥ÂÉè
				// ÂÅáËÆæÂêéÁ´ØËøîÂõûexpires_inÔºåÂçï‰Ωç‰∏∫Áßí
				const expiresIn = res.data.expires_in;
				const expireTime = Date.now() + expiresIn * 1000; // ËΩ¨Êç¢‰∏∫ÊØ´ÁßíÊó∂Èó¥Êà≥
				uni.setStorageSync('tokenExpire', expireTime);
		
		        // ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
		        uni.showToast({
		                title: 'ÁôªÂΩïÊàêÂäü',
		                icon: 'success',
		                duration: 1500,
		                success: () => { 
		                    // ÂÖ≥ÈîÆ‰øÆÊîπÁÇπÔºö‰ΩøÁî®switchTab
		                    uni.switchTab({
		                        url: '/pages/index/index',
		                        success: () => {
		                            console.log('Ë∑ØÁî±Ë∑≥ËΩ¨ÊàêÂäü');
		                        },
		                        fail: (err) => {
		                            console.error('Ë∑ØÁî±Ë∑≥ËΩ¨Â§±Ë¥•:', err);
		                            // Â§áÁî®Ë∑≥ËΩ¨ÊñπÊ°à
		                            uni.reLaunch({ 
		                                url: '/pages/index/index' 
		                            });
		                        }
		                    });
		                }
		            });
					
		
		        // ËÆ∞‰ΩèË¥¶Âè∑
		        if (this.rememberMe) {
		            uni.setStorageSync('rememberedAccount', this.form);
		        } else {
		            uni.removeStorageSync('rememberedAccount');
		        }
		
		    } catch (error) {
		        uni.showToast({
		            title: error.message || 'ÁôªÂΩïÂ§±Ë¥•',
		            icon: 'none'
		        });
		    } finally {
		        this.loading = false;
		    }
		},
    
		// ÂæÆ‰ø°ÁôªÂΩï
		onWechatLogin(e) {
			console.log('ÂæÆ‰ø°ÁôªÂΩï:', e)
		// ÂÆûÈôÖÂ§ÑÁêÜÂæÆ‰ø°ÁôªÂΩïÈÄªËæë
		},
    
		// ÊòæÁ§∫ÂçèËÆÆ
		showAgreement() {
			uni.navigateTo({ url: '/pages/webview?url=' + encodeURIComponent('https://localhost:3000/agreement') })
		},
    
		// ÊòæÁ§∫ÈöêÁßÅÊîøÁ≠ñ
		showPrivacy() {
			uni.navigateTo({ url: '/pages/webview?url=' + encodeURIComponent('https://localhost:3000/privacy') })
		},
    
		// ÈÄöÁî®Ë∑≥ËΩ¨
		navigateTo(url) {
			uni.navigateTo({ url })
		}
	}
}
</script>

